tation == $check-orientation {\n        @if ($qualifier == min-width) {\n            @media only screen and (orientation: $check-orientation) and (min-width: $size) {\n                @content;\n            }\n        } @else if ($qualifier == max-width) {\n            @media only screen and (orientation: $check-orientation) and (max-width: $size) {\n                @content;\n            }\n        }\n    }\n}\n\n@mixin bounded-select($orientation, $check-orientation, $min, $max) {\n    @if $orientation == none or $orientation == $check-orientation {\n        @media only screen and (orientation: $check-orientation) and (min-width: $min) and (max-width: $max) {\n            @content;\n        }\n    }\n}\n\n// this is a copy of respond-to. we don't want to preserve that method since the breakpoints may diverge from the old UI.\n@mixin is-screen-size($media: all, $orientation: none) {\n    @if $media == mobile {\n        @include query-select($orientation, portrait, $device-mobile-size, max-width) {\n            @content;\n        }\n        @include query-select($orientation, landscape, $device-landscape-mobile-size, max-width) {\n            @content;\n        }\n    } @else if $media == not-mobile {\n        @include query-select($orientation, portrait, $device-mobile-size + 1, min-width) {\n            @content;\n        }\n        @include query-select($orientation, landscape, $device-landscape-mobile-size + 1, min-width) {\n            @content;\n        }\n    } @else if $media == laptop {\n        @include bounded-select($orientation, portrait, $device-tablet-size + 1, $device-laptop-size) {\n            @content;\n        }\n        @include bounded-select($orientation, landscape, $device-landscape-tablet-size + 1, $device-landscape-laptop-size) {\n            @content;\n        }\n    } @else if $media == tablet {\n        @include bounded-select($orientation, portrait, $device-mobile-size + 1, $device-tablet-size) {\n            @content;\n        }\n        @include bounded-select($orientation, landscape, $device-landscape-mobile-size + 1, $device-landscape-tablet-size) {\n            @content;\n        }\n    } @else if $media == desktop {\n        @include query-select($orientation, portrait, $device-tablet-size, min-width) {\n            @content;\n        }\n        @include query-select($orientation, landscape, $device-landscape-tablet-size, min-width) {\n            @content;\n        }\n    } @else if $orientation == landscape {\n        @media only screen and (orientation: landscape) {\n            @content;\n        }\n    } @else if $orientation == portrait {\n        @media only screen and (orientation: portrait) {\n            @content;\n        }\n    }\n}\n\n\n@mixin hide-text-overflow {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n\n\n@mixin page-padding {\n    padding-left: $spacing-small;\n    padding-right: $spacing-small;\n}\n\n// Font Mixins\n$font-mckinsey-sans: \"McKinsey Sans\";\n$font-theinhardt: \"Theinhardt\", \"Helvetica Neue\", Arial, sans-serif;\n$font-bower: \"Bower\";\n\n@mixin bower-bold {\n    font-family: $font-bower;\n    font-style: normal;\n    font-weight: bold;\n    text-align: center;\n}\n\n@mixin mckinsey-sans {\n    font-family: $font-mckinsey-sans;\n    font-style: normal;\n    text-align: center;\n}\n\n@mixin display-1 {\n    @include bower-bold;\n    font-size: 3.75rem; // 60px\n    line-height: 1.2; // 72px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 4rem; // 64px\n        line-height: 1.187; // 76px\n    }\n}\n\n@mixin display-2 {\n    @include bower-bold;\n    font-size: 3.5rem; // 56px\n    line-height: 1.214; // 68px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 3.75rem; // 60px\n        line-height: 1.2; // 72px\n    }\n}\n\n@mixin display-3 {\n    @include bower-bold;\n    font-size: 3.25rem; // 52px\n    line-height: 1.153; // 60px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 3.5rem; // 56px\n        line-height: 1.214; // 68px\n    }\n}\n\n@mixin display-4 {\n    @include bower-bold;\n    font-size: 3rem; // 48px\n    line-height: 1.166; // 56px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 3.25rem; // 52px\n        line-height: 1.153; // 64px\n    }\n}\n\n@mixin display-5 {\n    @include bower-bold;\n    font-size: 2rem; // 30px\n    line-height: 1.833; // 34px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.25rem; // 36px\n        line-height: 1.642; // 40px\n    }\n}\n\n@mixin display-7 {\n    @include bower-bold;\n    font-size: 1.5rem; // 24px\n    line-height: 1.333; // 28px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1.75rem; // 28px\n        line-height: 1.142; // 32px\n    }\n}\n\n@mixin heading-1 {\n    @include mckinsey-sans;\n    font-size: 2.5rem; // 40px\n    line-height: 1.2; // 48px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.75rem; // 44px\n        line-height: 1.181; // 52px\n    }\n}\n\n@mixin heading-2 {\n    @include mckinsey-sans;\n    font-size: 2.25rem; // 36px\n    line-height: 1.111; // 44px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.5rem; // 40px\n        line-height: 1.181; // 48px\n    }\n}\n\n@mixin heading-3 {\n    @include mckinsey-sans;\n    font-size: 2rem; // 32px\n    line-height: 1.125; // 36px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.25rem; // 36px\n        line-height: 1.222; // 44px\n    }\n}\n\n@mixin heading-4 {\n    @include mckinsey-sans;\n    font-size: 1.75rem; // 28px\n    line-height: 1.142; // 32px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2rem; // 32px\n        line-height: 1.125; // 36px\n    }\n}\n\n@mixin heading-5 {\n    @include mckinsey-sans;\n    font-size: 1.5rem; // 24px\n    line-height: 1.333; // 288px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1.75rem; // 28px\n        line-height: 1.142; // 3